.form-container {
    background-color: red;
    margin: auto;
    max-width: 190em;
    background-color: #ccc;
    padding: 1.0em 2em;
    border-radius: 0.5em;
    text-align: right;
}
.form-container h3 {
    display: inline;
    text-align: right;
}
.modal-head {
    /* background-color: #ddd; */
    display: flex;
    align-items: center;
    border-radius: 0.5em;

}
form {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    font-size: 1em;
}
form input, form textarea {
    font-size: 1em;
    width: 16em;
}
form div{
    display: flex;
    justify-content: flex-end;
    text-align: right;
    width: 100%;
    padding: 0.25em;
    align-items: center;
}
form div label {
    color: #333;
    margin: 0 0.25em;
}
form div label::before {
    content: '* ';
    color: red;
}
div .error {
    display: flex;
    justify-content: flex-start;
    font-size: 0.8em;
    font-weight: 900;
    color: red;
    height: 1.8em;
    width: 4em;
}
